  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: local("poppins-regular"), url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff"); }

/* poppins-500 - latin */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  src: local("poppins-500"), url("../fonts/poppins-500.woff2") format("woff2"), url("../fonts/poppins-500.woff") format("woff"); }

/* cormorant-garamond-regular - latin */
@font-face {
  font-family: "Cormorant-Garamond";
  font-style: normal;
  font-weight: 400;
  src: local("Cormorant-Garamond"), url("../fonts/cormorant-garamond-regular.woff2") format("woff2"), url("../fonts/cormorant-garamond-regular.woff") format("woff"); }

/* cormorant-garamond-600 - latin */
