.navigation__upper-part {
  display: flex;
  max-width: 1366px;
  justify-content: space-between;
  padding-left: 100px;
  padding-right: 100px;
  position: relative;
  margin-bottom: 37px; }

.navigation__inner {
  margin-top: 30px;
  margin-bottom: 19px; }

.navigation__login-cart {
  padding-top: 17px;
  grid-template-columns: 1fr 1fr;
  display: grid;
  grid-gap: 35px; }

.navigation__login:hover,
.navigation__login:focus,
.navigation__cart-desktop:hover,
.navigation__cart-desktop:focus {
  opacity: 0.5; }

.navigation__input {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  border: none;
  max-width: 145px;
  max-height: 20px;
  margin-top: 17px; }

.navigation__upper-part::before {
  content: "";
  position: absolute;
  left: 80px;
  top: 20px;
  background-repeat: no-repeat;
  width: 14px;
  height: 14px;
  background-image: url("../img/search-icon.svg"); }

.navigation__logo {
  color: #a87b62; }

.navigation__menu {
  justify-content: center;
  display: flex; }

.navigation__list {
  list-style: none;
  margin: 0;
  padding-left: 0;
  display: grid;
  grid-template-columns: 1fr min-content min-content min-content min-content;
  max-width: 495px;
  grid-gap: 35px;
  justify-items: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px; }

.navigation__link:hover,
.navigation__link:focus {
  color: red; }

.navigation__link--active:hover,
.navigation__link--active:focus {
  color: #1d1613; }

.header {
  margin-bottom: 130px;
  position: relative; }

.header::before {
  content: "";
  position: absolute;
  top: -70px;
  background-repeat: no-repeat;
  height: 1px;
  border: 1px solid #f6f5f4;
  width: 100%; }

.header__inner {
  padding-right: 0;
  padding-left: 0;
  position: relative;
  max-width: 1366px; }

.header__image {
  max-height: 463px; }

.header__information {
  position: absolute;
  top: 38%;
  left: 50%;
  transform: translateX(-50%);
  color: #ffffff; }

.header__title {
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 45px;
  line-height: 58.5px;
  margin-top: 0;
  margin-bottom: 5px;
  letter-spacing: 3px;
  text-transform: uppercase; }

.header__text {
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 400;
  font-size: 38px;
  line-height: 49.4px;
  margin: 0;
  text-align: center;
  font-style: italic; }

.slider {
  margin-bottom: 130px; }

.slider__wrapper {
  margin-bottom: 60px; }

.slider__inner {
  max-width: 1200px; }

.slider__header {
  display: flex;
  justify-content: center;
  position: relative;
  margin-bottom: 60px; }

.slider__info {
  display: flex;
  flex-direction: column;
  text-align: center; }

.slider__title {
  margin-top: 0;
  margin-bottom: 35px;
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 44.2px; }

.slider__link:hover,
.slider__link:focus,
.slider__link-title:hover,
.slider__link-title:focus {
  opacity: 0.5; }

.slider__link-title {
  font-weight: 500;
  position: relative; }

.slider__link-title::after {
  content: "";
  position: absolute;
  left: 10px;
  top: 20px;
  background-repeat: no-repeat;
  height: 1px;
  border: 1px solid #a87b62;
  width: 80px; }

.slider__button {
  position: absolute;
  border: none;
  background-color: inherit;
  cursor: pointer;
  top: 35px; }

.slider__button--right {
  right: 0; }

.slider__button--left {
  left: 0; }

.slider__button:hover,
.sloder__button:focus {
  opacity: 0.5; }

.slider__wrapper-inner {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 30px; }

.slider__information {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 25px; }

.slider__text {
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 500; }

.slider__price {
  margin: 0;
  font-size: 14px;
  line-height: 18.2px;
  font-weight: 700; }

.slider__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  justify-content: center; }

.slider__buttons-mobile {
  display: none; }

.slider__link--tablet {
  display: none; }

.slider__item-button:not(:last-child) {
  padding-right: 15px; }

.blog {
  margin-bottom: 130px; }

.blog__inner {
  max-width: 1170px;
  padding-left: 0;
  padding-right: 0; }

.blog__header {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 70px;
  flex-direction: column; }

.blog__title {
  margin-top: 0;
  margin-bottom: 35px;
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 44.2px;
  letter-spacing: 3px; }

.blog__link-header,
.blog__link {
  font-weight: 500;
  position: relative; }

.blog__link-header::after,
.blog__link::after {
  content: "";
  position: absolute;
  left: 0;
  top: 20px;
  background-repeat: no-repeat;
  height: 1px;
  border: 1px solid #a87b62; }

.blog__link:hover,
.blog__link:focus,
.blog__link-header:hover,
.blog__link-header:focus {
  opacity: 0.5; }

.blog__link-header::after {
  width: 64px; }

.blog__link::after {
  width: 97px; }

.blog__item-tablet {
  display: none; }

.blog__gallery {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px; }

.blog__set-of-pictures {
  display: flex;
  flex-direction: column; }

.blog__item {
  display: grid;
  max-width: 570px;
  grid-template-columns: 1fr 1fr; }

.blog__item:not(:last-child) {
  margin-bottom: 30px; }

.blog__item-desktop {
  position: relative; }

.blog__information--desktop {
  position: absolute;
  bottom: 0;
  right: 0;
  min-width: 285px;
  min-height: 300px; }

.blog__information {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f0eAe4; }

.blog__text,
.blog__text-first,
.blog__data {
  margin-top: 0; }

.blog__data {
  margin-bottom: 25px;
  font-weight: 500;
  font-size: 14px;
  line-height: 18.2px;
  color: #9b9997; }

.blog__text,
.blog__text-first {
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 33.8px; }

.blog__text-first {
  margin-bottom: 0; }

.blog__text {
  margin-bottom: 50px; }

.blog__image,
.blog__image-desktop {
  height: 100%; }

.faq {
  margin-bottom: 108px; }

.faq__inner {
  max-width: 970px;
  padding-left: 0;
  padding-right: 0; }

.faq__header {
  margin-bottom: 60px;
  text-align: center; }

.faq__title-header {
  margin-top: 0;
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 44.2px;
  letter-spacing: 3px; }

.faq__item {
  position: relative;
  min-width: 970px; }

.faq__item:not(:last-child) {
  margin-bottom: 99px; }

.faq__item:not(:last-child)::after {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
