.faq__title {
  margin-top: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 18px;
  line-height: 23.4px; }

.faq__text {
  margin: 0; }

.faq__button {
  position: absolute;
  top: 0;
  right: 115px;
  border: none;
  background-color: inherit;
  cursor: pointer; }

.faq__button:hover,
