.catalog__title {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.4px; }

.text-for-form:disabled {
  opacity: 0.3; }

.catalog__form {
  width: 25.5%;
  /*max-height: 780px;*/
  display: flex;
  padding-right: 139px; }

.catalog__item {
  margin: 0;
  padding: 0;
  border: none;
  margin-bottom: 2px; }

.catalog__range {
  width: 260px;
  margin-bottom: 54px; }

.filter-item legend {
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  font-weight: 700;
  box-sizing: content-box;
  letter-spacing: -0.9px; }

.range__controls {
  position: relative;
  height: 41px;
  background-color: #ffffff;
  border-radius: 5px;
  margin-bottom: 17px;
  padding-top: 39px;
  padding-right: 20px;
  padding-left: 20px;
  box-sizing: content-box; }

.range__scale {
  height: 2px;
  background: #f0eae4; }

.range__bar {
  height: 2px;
  background: #a87b62;
  width: 78%;
  margin-left: 11%; }

.range__toggle {
  position: absolute;
  top: 33px;
  left: 0;
  width: 14px;
  height: 14px;
  padding: 0;
  background-color: #a87b62;
