.range__toggle--max {
  left: 61%; }

.catalog__checkbox {
  border: none;
  padding: 0;
  letter-spacing: -1.3px;
  margin-left: 0;
  margin-right: 0; }

.checkbox__form {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 32px;
  position: relative; }

.checkbox__line {
  padding-bottom: 16px; }

.checkbox__line:nth-child(1) {
  letter-spacing: 0.1px; }

.checkbox__label {
  padding-left: 20px; }

.check {
  padding-left: 36px;
  letter-spacing: 0.1px; }

.checkbox__input {
  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none; }

.checkbox__box {
  position: absolute;
  width: 25px;
  height: 25px;
  margin-left: 0;
  background-image: url("../../img/chekbox-not-checked.svg");
  background-repeat: no-repeat;
  left: 0; }

.checkbox__input:checked + .checkbox__box {
  background-image: url("../../img/checkbox-checked.svg");
  background-repeat: no-repeat; }

.checkbox__input:hover + .checkbox__box,
.checkbox__input:focus + .checkbox__box {
  background-image: url("../../img/chekbox-not-checked.svg");
  background-repeat: no-repeat; }

.checkbox__input:checked:hover + .checkbox__box,
.checkbox__input:checked:focus + .checkbox__box {
  background-image: url("../../img/checkbox-checked.svg");
  background-repeat: no-repeat; }

.catalog__inner {
  display: flex; }
